<template>
  <div>
    <div class="container">

      <div class="row">
        <table class="table">
          <thead class="thead-default">
          <tr>
            <th>文件名</th>
            <th>时间</th>
            <th>备注</th>
            <th>状态</th>
            <th>删除</th>
            <th>查看</th>
            <th>下载</th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <th scope="row">test.xls</th>
            <td>{{ test | formatDate }}</td>
            <td>remarks</td>
            <td>上传中</td>
            <td><a href="">删除</a></td>
            <td><a href="">在线查看</a></td>
            <td><a href="">下载</a></td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
    export default{
      components: {
      },
      filters: {
        formatDate(time) {
          const date = new Date(time);
          const o = {
            month: date.getMonth() + 1,
            day: date.getDate(),
            hours: date.getHours(),
            minutes: date.getMinutes(),
            years: date.getFullYear(),
          };
          return `${o.years}-${o.month}-${o.day}`;
        },
      },
      data() {
        return {
          test: 1479297463229,
          msg: 'Welcome to Your Vue.js App',
          columns: ['id', 'name', 'status', 'operation'],
          tableData: [
            { name: 'John', status: '已计算' },
            { name: 'Jake', status: '待计算' },
          ],
        };
      },
    };
</script>
<style>
  label {
    display: inline-block;
    margin-bottom: 0;
  }
  #vue-tables-options {
    text-align: center;
  }

  table {
    text-align: left;
  }

  h2 {
    margin-bottom: 10px;
  }

  thead tr:nth-child(2) th {
    font-weight: normal;
  }

  .VueTables__sortable {
    cursor: pointer;
  }

</style>



